import { MediaNormal } from '@/components/app/media';
import { ImageList, ImageListItem, Typography, Box } from '@mui/material';
import { useShadowNextUrgesByShadowQuery } from '@/schema';
import { useParams } from 'react-router-dom';
import Placeholder from '@/components/base/placeholder';
import { useNavigate } from 'react-router-dom';

export default function NextPlay() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { data, error, loading } = useShadowNextUrgesByShadowQuery({
    variables: {
      shadow_id: id!,
    },
  });
  if (loading) {
    return <div>loading</div>;
  }
  if (error) {
    return <div>error</div>;
  }
  return (
    <Box py={2}>
      <Typography variant="subtitle1" gutterBottom>
        接下来播放
      </Typography>
      {data?.shadow_next_urges_by_shadow.length ? (
        <ImageList rowHeight="auto" cols={4}>
          {data.shadow_next_urges_by_shadow.map((item, idx) => {
            return (
              <ImageListItem cols={1} key={idx}>
                <MediaNormal
                  {...item}
                  onClickRoot={() => navigate(`/shadow/${item.id}`)}
                  onClickAuthor={() =>
                    navigate(`/profile/${item.author.username}`)
                  }
                />
              </ImageListItem>
            );
          })}
        </ImageList>
      ) : (
        <div>
          <Placeholder subtitle="暂无可播放资源" />
        </div>
      )}
    </Box>
  );
}
